<template>
  <el-container class="clearfix">
  	<el-header style="width: 100%; height: auto; padding: 0; margin-bottom: 20px;">
  		<el-row class="demo-autocomplete" style="width: 200px; margin-right: 10px; float: left;">
  			<el-col :span="24">
  				<el-autocomplete class="inline-input" v-model="state2" :fetch-suggestions="querySearch" placeholder="" :trigger-on-focus="false" @select="handleSelect" clearable></el-autocomplete>
  			</el-col>
  		</el-row>
  		<el-button type="primary" style="float: left;" @click="search()"></el-button>
  		<el-button style="float: left;" @click="firing('', '3')"></el-button>
  		<el-dialog :title="title" :visible.sync="centerDialogVisibleAdd" width="20%" center>
  			<p class="upPwd">
  				<input class="el-input__inner" type="text" name="" id="" value="" v-model="name" @keyup.enter="addName()" />
  			</p>
  			<span slot="footer" class="dialog-footer">
              <el-button @click="cancel('3')"> </el-button>
              <el-button type="primary" @click="addName()" :loading="addNameLoading"> </el-button>
            </span>
  		</el-dialog>
      <div class="clearfix"></div>
      <el-alert title="" description="," type="warning" show-icon style="width: 600px; margin: 20px 0;"></el-alert>
  	</el-header>
  	<el-footer style="height: auto;">
  		<el-table v-loading="loadingP" :data="groupLabel3" class="clearfix" :stripe="true" size="mini" style="width: 60%; min-height: 474px;">
  			<el-table-column prop="gid" label="id" width="50" align="center"></el-table-column>
  			<el-table-column prop="name" label="" min-width="150" align="center"></el-table-column>
  			<el-table-column prop="lid" label="" min-width="300" align="center">
  				<template slot-scope="scope">
            <el-tag v-if="scope.row.lid === null" type="info"></el-tag>
  					<span v-if="scope.row.lid !== null" v-html="scope.row.lid === null ? '' : scope.row.lid"></span>
  				</template>
  			</el-table-column>
        <el-table-column prop="labelList" label="" width="100" align="center"></el-table-column>
  			<el-table-column prop="state" label="" width="100" align="center">
  				<template slot-scope="scope">
            <el-tag v-if="scope.row.state === '1'" type="success"></el-tag>
            <el-tag v-if="scope.row.state === '2'" type="danger"></el-tag>
  				</template>
  			</el-table-column>
  			<el-table-column prop="webShow" label="" width="100" align="center">
  				<template slot-scope="scope">
  					<svg v-if="scope.row.webShow === '1'" class="icon" aria-hidden="true"><use xlink:href="#icon-dui"></use></svg>
  					<svg v-if="scope.row.webShow === '0'" class="icon" aria-hidden="true"><use xlink:href="#icon-cuo"></use></svg>
  				</template>
  			</el-table-column>
  			<el-table-column label="" align="center" width="100">
  				<template slot-scope="scope">
            <el-button size="mini" type="primary" circle icon="el-icon-edit" title="" @click="firing(scope.row, '1')" style="margin-left: 20px;"></el-button>
  					<el-dialog :title="title" :visible.sync="centerDialogVisibleP" width="21%" center style="margin-left: -15vw;">
  						<div class="demo-input-suffix">
  							:
  							<el-input style="width: auto;" class="el-input--suffix" placeholder=""  name="" id="" v-model="row.name" clearable></el-input>
  						</div>
  						<div class="demo-input-suffix">
  							:
  							<el-radio-group v-model="row.state" style="margin-left: 43px;">
                    <el-radio label="1"></el-radio>
                    <el-radio label="2"></el-radio>
                </el-radio-group>
                <span class="intro"><b></b></span>
  						</div>
  						<div class="demo-input-suffix">
  							:
  							<el-radio-group v-model="row.webShow" style="margin-left: 15px;">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
                <span class="intro"></span>
  						</div>
  						<span slot="footer" class="dialog-footer">
  		            <el-button @click="cancel('1')"> </el-button>
  		            <el-button type="primary" @click="handleUpdate()" :loading="handleUpdateLoading"> </el-button>
  		          </span>
  					</el-dialog>
            <el-button size="mini" type="danger" icon="el-icon-delete" circle title="" @click="dataDel(scope.$index, scope.row.gid)" :disabled="scope.row.labelList === '0'  ? false : true"></el-button>
  				</template>
  			</el-table-column>
  		</el-table>
  	</el-footer>
  	<div class="block" style="width: 470px;margin: 0 0 0 20%;">
  		<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage1" :page-size="dataList" layout="total, prev, pager, next" :total="groupLabel2.length">
  		</el-pagination>
  	</div>
  </el-container>
</template>

<script>
  import { mapActions, mapGetters, mapMutations } from 'vuex'
  export default {
    inject: ['reload'],
    props: {
      myGroupLabel: Array,
      myLoading: Boolean
    },
    name: 'backstage',
    components: {},
    data() {
      return {
        restaurants: [],
        state1: '',
        state2: '',
        loadingP: true,
        PList: [],
        tList: [],
        groupLabel: [],
        groupLabel2: [],
        groupLabel3: [],
        uId: '',
        title: '',
        name: '',
        addNameLoading: false,
        row: {},
        handleUpdateLoading: false,
        centerDialogVisibleP: false,
        centerDialogVisibleT: false,
        centerDialogVisibleAdd: false,
        currentPage1: 1,
        dataList: 10
      }
    },
    watch: {
    	// minType
    	groupLabel: function(newQuestion, oldQuestion) {
    		let _this = this
    		this.groupLabel2 = []
    		this.groupLabel3 = []
    		this.groupLabel2 = this.groupLabel
    		if(this.groupLabel.length !== 0) {
    			this.groupLabel.find((obj, index) => {
    				if(index < _this.dataList) {
    					_this.$set(_this.groupLabel3, _this.groupLabel3.length, obj)
    				}
    			})
    		}
    	},
      myLoading: function(newQuestion, oldQuestion){
        this.loadingP = this.myLoading
      }
    },
    methods: {
      ...mapActions([
        'setPublicInfo',
        'getPublicInfo',
        'labelsAdd',
        'labelssave',
        'labelsDel',
        'setOperationInfo',
      ]),
      // 
      search () {
      	this.loadingP = true
      	let temp = []
      	let _this = this
      	this.groupLabel2 = []
      	this.groupLabel3 = []
      	if (this.state2.length !== 0) {
      		this.groupLabel.find((obj, index) => {
      			obj.name.indexOf(_this.state2) !== -1 ? temp.push(obj) : obj = obj
      		})
      		if (temp.length !== 0) {
      			temp.find((obj, index) => {
      				_this.groupLabel2.push(obj)
      			})
      			this.handleCurrentChange(1)
      		}
      	} else {
      		this.groupLabel.find((obj, index) => {
      			_this.$set(_this.groupLabel2, _this.groupLabel2.length, obj)
      		})
      		this.handleCurrentChange(1)
      	}
      	this.loadingP = false
      },
      // 
      handleSizeChange(val) {
      	//      console.log(' ' + val + '');
      },
      // 
      handleCurrentChange(val) {
      	//      console.log(': ' + val)
      	let _this = this
      	// 
      	let begin = (this.dataList * val) - this.dataList
      	// 
      	let end = (this.dataList * val) - 1
      	// 
      	this.groupLabel3 = []
      	// 
      	this.groupLabel2.find((obj, index) => {
      		if(index >= begin && index <= end) {
      			_this.$set(_this.groupLabel3, _this.groupLabel3.length, obj)
      		}
      	})
      },
      handleProhibit(index, row, id) {
      	if(id === false) {
      		labelsDisable(this, row.gid)
      	} else if(id === true) {
      		labelsEnable(this, row.gid)
      	}
      },
      // 
      handleUpdate() {
        let _this = this
      	// 
      	this.handleUpdateLoading = true
      	if(this.row.name.replace(/\s*/g,"").length === 0) {
      		this.$alert('', '', { confirmButtonText: '' })
      	} else if(this.row.state.length > 1) {
      		this.$alert('', '', { confirmButtonText: '' })
      	} else if(this.row.webShow.length > 1) {
      		this.$alert('', '', { confirmButtonText: '' })
      	} else {
      		this.labelssave({gid: this.row.gid, name: this.row.name, state: this.row.state, webShow: this.row.webShow})
            .then(function (response) {
              if (response.code === 200) {
                _this.setOperationInfo({_this:_this, type:22})
                _this.$message({message: response.msg, type: 'success'})
                // vuex
                _this.setPublicInfo()
                // app.vue
                _this.reload()
              }
            })
            .catch(function (error) {
              _this.$alert(error.msg, {confirmButtonText: ''})
              // app.vue
              _this.reload()
            })
      	}
      },
      // 
      dataDel(index, gid) {
        let _this = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          _this.labelsDel({gid: gid})
            .then(function (response) {
              if (response.code === 200) {
                _this.setOperationInfo({_this:_this, type:23, id:gid})
                _this.$message({message: response.msg, type: 'success'})
                // vuex
                _this.setPublicInfo()
                // app.vue
                _this.reload()
              }
            })
            .catch(function (error) {
              _this.$alert(error.msg, {confirmButtonText: ''})
              // app.vue
              _this.reload()
            })
        }).catch(() => {
          // 
        })
      },
      //  
      firing(row, id) {
      	// 
      	this.row = JSON.stringify(row)
      	this.row = JSON.parse(this.row)
      	if(id === '1') {
      		this.centerDialogVisibleP = true
      		this.title = '' + row.name + ''
      	} else if(id === '3') {
      		this.centerDialogVisibleAdd = true
      		this.title = ''
      	}
      },
      // /
      cancel(id) {
      	if(id === '1') {
      		this.centerDialogVisibleP = false
      	} else if(id === '2') {
      		this.centerDialogVisibleT = false
      	} else if(id === '3') {
      		this.centerDialogVisibleAdd = false
      	} else if(id === '4') {
      		this.centerDialogVisibleAdd = false
      	}
      	this.row = {}
      	this.name = ''
      },
      // /
      addName() {
        let _this = this
      	if(this.name.replace(/\s*/g,"").length === 0) {
      		this.$alert('', '', {
      			confirmButtonText: ''
      		})
      	} else {
      		// 
      		this.addNameLoading = true
      		// 
          this.labelsAdd({name: this.name.replace(/\s*/g,"")})
            .then(function (response) {
              if (response.code === 200) {
                _this.setOperationInfo({_this:_this, type:21, name: _this.name.replace(/\s*/g,"")})
                _this.$message({message: response.msg, type: 'success'})
                // vuex
                _this.setPublicInfo()
                // app.vue
                _this.reload()
              }
            })
            .catch(function (error) {
              _this.$alert(error.msg, {confirmButtonText: ''})
              // app.vue
              _this.reload()
            })
      	}
      },
      querySearch(queryString, cb) {
      	var restaurants = this.restaurants;
      	var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      	//  callback 
      	cb(results);
      },
      createFilter(queryString) {
      	return(restaurant) => {
      		return(restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1);
      	};
      },
      loadAll() {
      	let temp = []
      	this.groupLabel.find((obj, index) => {
      		temp.push({
      			"value": obj.name,
      			"address": obj.gid
      		})
      	})
      	return temp
      },
      handleSelect(item) {
      	console.log(item);
      }
    },
    mounted() {
    	this.restaurants = this.loadAll();
    },
    created() {
      this.groupLabel = this.myGroupLabel
      this.loadingP = this.myLoading
    }
  }
</script>

<style lang="less" scoped>
@import '~LESS/color.less';
.intro {color: gray;font-weight: normal;font-size: 12px;border: 1px solid silver; border-left-width: 4px;padding: 3px 4px 2px 4px; line-height: 14px; height: 20px;margin-left: 6px;}
.icon {width: 1.5em;height: 1.5em; vertical-align: -0.15em;fill: currentColor;overflow: hidden; margin-left: .5em;}
.titleType{height:60px;line-height:60px;text-align:left}
.titleType b{display:block;float:left;width:50%;height:60px;line-height:60px;font-size:20px}
.demo-input-suffix{margin-bottom:15px}
.demo-input-suffix .el-input{margin-left:15px}
</style>



